<template>
	<view class="body">
		<view class="head">
			<image class="banner" src="../../static/volunteer/result.png" mode="widthFix"></image>
			<view class="model">
				<view class="title">当前该院校专业拟报名人数</view>
				<view class="num">{{data.total || 0}}人</view>
				<view class="buttons">
					<view class="review" @click="backComplete">重新填报</view>
					<view class="look" @click="toInquiry">数据查看</view>
				</view>
			</view>
		</view>
		<view class="info">
			<view class="cell">
				<view>姓名：</view>
				<view class="text">{{data.data.name}}</view>
			</view>
			<view class="cell">
				<view>报考省份：</view>
				<view class="text">{{data.data.area_name}}</view>
			</view>
			<view class="cell">
				<view>报考院校：</view>
				<view class="text">{{data.data.school_name}}</view>
			</view>
			<view class="cell">
				<view>报考专业：</view>
				<view class="text">{{data.data.major_name}}</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { volunteer, } from '@/common/http.api.js';
	export default{
		data() {
			return {
				token:'',
				data:{}
			}
		},
		onLoad() {
			
		},
		onShow() {
			this.token = store.state.vuex_token
			volunteer({token:this.token}).then(res=>{
				console.log(res)
				if(res.data.data.data!=null){
					this.data=res.data.data
				}
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 回到填写页面
			backComplete() {
				uni.setStorage({
					key: 'id',
					data: this.data.data.id,
					success: function () {
						console.log('success');
					}
				});
				uni.navigateTo({
					url:'/pages/volunteer/complete?isUpdate='+true
				})
			},
			toInquiry() {
				uni.navigateTo({
					url:'/pages/volunteer/inquiry'
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 2vh 0;
		min-height: 96vh;
		background-color: #f7f6fb;
		.head{
			width: 100vw;
			overflow: auto;
			position: relative;
			height: 340rpx;
			.banner{
				width: 90vw;
				position: absolute;
				left: 5%;
				right: 5%;
				top: 0;
				bottom: 0;
				z-index: 1;
				height: 340rpx;
				border-radius: 20rpx;
			}
			.model{
				width: 90vw;
				position: absolute;
				left: 5%;
				right: 5%;
				top: 0;
				bottom: 0;
				z-index: 2;
				padding-top: 30rpx;
				text-align: center;
				.title{
					font-size: 36rpx;
					color: #fff;
					letter-spacing: 2rpx;
					line-height: 88rpx;
				}
				.num{
					font-size: 54rpx;
					color: #ffd753;
					font-weight: 700;
					line-height: 100rpx;
				}
				.buttons{
					display: flex;
					justify-content: space-between;
					margin: 0 100rpx;
					margin-top: 20rpx;
					.review{
						border-radius: 50rpx;
						line-height: 58rpx;
						font-size: 28rpx;
						text-align: center;
						width: 220rpx;
						background-color: #fff;
						color: #405efa;
					}
					.look{
						border-radius: 50rpx;
						line-height: 58rpx;
						font-size: 28rpx;
						text-align: center;
						width: 220rpx;
						background-color: #ffd656;
						color: #fff;
					}
				}
			}
		}
		.info{
			margin: 0 5%;
			margin-top: 30rpx;
			padding: 30rpx 5%;
			background-color: #fff;
			border-radius: 20rpx;
			.cell{
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: 50rpx;
				font-size: 28rpx;
				.text{
					width: 70%;
					padding: 0 10rpx;
					border-bottom: 1rpx solid #eee;
					line-height: 58rpx;
				}
			}
			.cell:last-child{
				margin-bottom: 0;
			}
		}
	}
</style>